import {ModuleWithProviders} from "@angular/core"
import {RouterModule, Routes} from "@angular/router";

import { AgentesComponent } from './agentes.component';
import { AgenteFormComponent } from './agente-form/agente-form.component';

export const routes:Routes = [
    {
        path: '', 
        component: AgentesComponent,
        data: {pageTitle: ''}
    },
    {
        path: 'crear',
        component: AgenteFormComponent,
        data: {pageTitle: 'Crear Agente'}
    },
    {
        path: 'editar/:id',
        component: AgenteFormComponent,
        data: {
            pageTitle: 'Editar Agente'
        }
    }
];

export const routing = RouterModule.forChild(routes)



