import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PersonalService } from './../../shared/personal.service';
import { personalValidationOptions } from './../../shared/personal-validation-options';

declare var $: any;

@Component({
  selector: 'app-agente-form',
  templateUrl: './agente-form.component.html',
  styles: []
})
export class AgenteFormComponent implements OnInit {
  mode: string;
  agente: any = {};
  isChanginPassword = false;
  validationOptions = personalValidationOptions;

  private $form;
  private lastEmail = '';

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private personalService: PersonalService
  ) {
    this.mode = this.route.snapshot.params.id ? 'Editar' : 'Crear';
  }

  ngOnInit() {
    this.agente = {
      ...this.personalService.personalModel,
      roles: [2]
    };

    this.route.params
      .subscribe(params => {
        this.render(params.hasOwnProperty('id') ? params['id'] : false);
      });
  }

  render(id = false) {
    if (id) {
      this.personalService.getPersonal(id)
        .subscribe((data: any) => {
          this.agente = data.result;
        });
    }
  }

  update(ev) {
    this.agente[ev.target.name] = ev.target.value;
  }

  save(agente) {
    if (!agente.hasOwnProperty('_id')) {
      this.personalService.createPerfil(agente)
        .subscribe((data: any) => this.handleSuccess(data));
    } else {
      if (typeof agente.newPassword !== 'undefined') {
        agente['password'] = agente.newPassword;
      } else {
        delete agente['password'];
      }
      this.personalService.updatePerfil(agente)
        .subscribe((data: any) => this.handleSuccess(data));
    }
  }

  handleSuccess(status) {
    if (status.success) {
      this.router.navigate(['usuarios', 'agentes']);
    } else {
      if (status.code === 11000) {
        this.$form.find('input[name=email]').data('last-mail', this.agente.email);
        this.$form.data('bootstrapValidator').revalidateField('email');
      }
    }
  }

  onValidationSuccess(e) {
    this.$form = $(e.currentTarget);
    if (e.type === 'success') {
      this.save(this.agente);
    }
  }

}



